import Vue from 'vue';

export default Vue.extend({
    props: {
        query: {
            type: String,
            required: true,
            default: '',
        },
        query_2: {
            type: String,
            required: false,
            default: '',
        },
    },
    data() {
        return {
            /** 検索文字列を入れたURL */
            queryUrl: '',
            queryUrl_2: '',
        };
    },
    mounted() {
        if (this.query)
            this.queryUrl = `https://www.google.com/maps?output=embed&q=${this.query}&t=m&hl=ja&z=17&iwloc=A`;
        if (this.query_2)
            this.queryUrl_2 = `https://www.google.com/maps?output=embed&q=${this.query_2}&t=m&hl=ja&z=17&iwloc=A`;
        // t=m : 表示モード
        // hl=ja : 言語
        // z : 縮尺
        // q : '緯度,経度' or 文字列
    },
});
